/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
.box-header {
  position: fixed;
  top: -90px;
  width: 100%;
  height: auto;
  min-width: 320px;
  text-align: left;
  order: 1;
  background: rgba(31, 31, 31, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 100;
  opacity: 0;
  transition: 0.4s ease-out;
}

.box-header.visible {
  top: 0 !important;
  opacity: 1;
  transition: background .6s linear, top .6s linear;
}

.box-header.light {
  background: #fff;
  border-bottom: 1px solid rgba(30, 30, 30, 0.1);
}

.box-header.light .header-info .call-us__link,
.box-header.light .login__text,
.box-header.light .form-search__icon,
.box-header.light .header-favorites,
.box-header.light .header-order {
  color: #1E1E1E;
}

.box-header.light .header-info .call-us__link:hover,
.box-header.light .login__text:hover,
.box-header.light .form-search__icon:hover,
.box-header.light .header-favorites:hover,
.box-header.light .header-order:hover {
  color: #0FCB0B;
}

.box-header.light .header-list__link {
  color: #1E1E1E;
}

.box-header.light .header-info .call-us__title {
  color: rgba(30, 30, 30, 0.6);
}

.box-header.light .login::before,
.box-header.light .login::after,
.box-header.light .form-search::before,
.box-header.light .header-favorites::before,
.box-header.light .header-favorites::after,
.box-header.light .header-order::before {
  background-color: rgba(30, 30, 30, 0.2);
}

.box-header.light .header-btn.light span {
  background: #FFFFFF;
}

.box-header.light .header-btn span {
  background: #1E1E1E;
}

.box-header.light .header-btn:hover span {
  background: #0FCB0B;
}

.box-header.light .form-submit {
  background-color: #fff;
}

.box-header.light .form-submit__btn.btn,
.box-header.light .form-submit__input {
  color: #1E1E1E;
}

.box-header.light .form-submit .input-border::before {
  background-color: #000;
}

.box-header.light .form-submit__input::placeholder {
  color: rgba(30, 30, 30, 0.2);
}

.box-header__wrap {
  height: 80px;
  display: flex;
  align-items: center;
}

.box-header .call-us {
  padding-top: 2px;
}

.box-header .call-us__icon {
  color: rgba(15, 203, 11, 0.5);
  font-size: 20px;
  margin-right: 0px;
}

.header-order {
  display: flex;
  align-items: center;
  padding: 0 24px;
  position: relative;
  font-size: 18px;
  cursor: pointer;
  color: #fff;
  transition: 0.4s ease-out;
}

.header-order:hover {
  color: #0fcb0b;
}

.header-order::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.header-favorites {
  display: flex;
  align-items: center;
  padding: 0 24px;
  position: relative;
  font-size: 18px;
  cursor: pointer;
  color: #fff;
  margin-right: 20px;
  transition: 0.4s ease-out;
}

.header-favorites:hover {
  color: #0fcb0b;
}

.header-favorites::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.header-favorites::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.header-list {
  display: flex;
}

.header-list-wrap {
  padding-left: 50px;
}

.header-list__item {
  margin-right: 32px;
  position: relative;
}

.header-list__item::after {
  content: "";
  position: absolute;
  bottom: -31px;
  left: 0;
  width: 0;
  height: 3px;
  background-color: transparent;
  transition: 0.4s ease-out;
}

.header-list__item:last-child {
  margin-right: 0;
}

.header-list__item:hover::after {
  width: 100%;
  background-color: #0FCB0B;
}

.header-list__item.active::after {
  width: 100%;
  background-color: #0FCB0B;
}

.header-list__link {
  font-weight: 600;
  line-height: normal;
  font-size: 16px;
  color: #FFFFFF;
}

.header-info {
  margin-left: auto;
  display: flex;
}

.form-search {
  display: flex;
  align-items: center;
  padding: 0 24px;
  margin-left: 20px;
  position: relative;
}

.form-search::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.form-search__icon {
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  transition: 0.4s ease-out;
}

.form-search__icon:hover {
  color: #0fcb0b;
}

.form-search.active .form-search__icon {
  color: #0fcb0b;
}

.form-search.active .form-submit {
  transform: perspective(600px) rotateX(0deg);
  opacity: 1;
  z-index: 100;
}

.form-submit {
  position: fixed;
  top: 81px;
  left: 0;
  width: 100%;
  height: 270px;
  background: #2E2E2E;
  box-shadow: 0px 12px 16px rgba(0, 0, 0, 0.16);
  opacity: 0;
  z-index: -1;
  backface-visibility: hidden;
  transform: perspective(600px) rotateX(90deg);
  transform-origin: 0% 0%;
  transition: 0.4s ease-out;
}

.form-submit__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.form-submit__search {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 532px;
}

.form-submit__input {
  height: 56px;
  width: calc(100% - 94px);
  padding-right: 20px;
  border: none;
  outline: none;
  background-color: transparent;
  font-weight: bold;
  font-size: 18px;
  line-height: 46px;
  letter-spacing: 1px;
  color: #FFFFFF;
}

.form-submit__input::placeholder {
  font-weight: 600;
  font-size: 46px;
  line-height: 46px;
  position: relative;
  top: 10px;
  color: rgba(255, 255, 255, 0.23);
}

.form-submit .input-border::before {
  background-color: rgba(255, 255, 255, 0.23);
}

.form-submit__btn.btn {
  background-color: transparent;
  max-width: 94px;
  position: relative;
  padding: 10px;
  color: #fff;
}

.form-submit__submit {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.login {
  display: flex;
  align-items: center;
  padding: 0 22px;
  position: relative;
}

.login::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.login::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.2);
}

.login:hover .login__text {
  color: #0FCB0B;
}

.login__icon {
  color: rgba(15, 203, 11, 0.5);
  margin-right: 7px;
  font-size: 17px;
  text-shadow: 0 0;
}

.login__text {
  color: #fff;
  font-weight: 600;
  line-height: normal;
  font-size: 16px;
  transition: 0.4s ease-out;
}

.header-btn {
  display: block;
  width: 29px;
  cursor: pointer;
  z-index: 1000;
}

.header-btn:hover span {
  background-color: #0FCB0B;
}

.header-btn-wrap {
  padding-left: 22px;
  display: flex;
  align-items: center;
}

.header-btn span {
  display: block;
  height: 2px;
  width: 29px;
  background: #FFFFFF;
  border-radius: 20px;
  position: relative;
  transition: 0.4s ease-out;
}

.header-btn span:nth-child(1) {
  width: 18px;
}

.header-btn span:nth-child(2) {
  right: 0;
  margin: 6px 0;
  width: 20px;
  margin-left: auto;
}

.header-btn.active span:nth-child(1) {
  transform: rotate(-135deg);
  left: 0px;
  top: 8px;
  width: 26px;
}

.header-btn.active span:nth-child(2) {
  opacity: 0;
  right: 25px;
}

.header-btn.active span:nth-child(3) {
  transform: rotate(135deg);
  left: 0px;
  top: -8px;
  width: 26px;
}

.header-menu {
  overflow: hidden;
  border-radius: 50%;
  border-top-right-radius: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 40px 15px;
  width: 100%;
  height: 100%;
  transform: scale(0.5);
  transform-origin: top right;
  top: -50%;
  right: -50%;
  z-index: 999;
  background: url(../img/header-menu-bg.jpg) no-repeat center center;
  background-size: cover;
  opacity: 0;
  transition: all .7s, border-radius .7s ease-in-out;
}

.header-menu.active {
  overflow-y: auto;
  top: 0;
  right: 0;
  position: fixed;
  transform: scale(1);
  border-radius: 0%;
  opacity: 1;
}

.header-menu.item-slide .header-menu-list__item {
  opacity: 1;
  transform: translateY(0px);
}

.header-menu .social-list__link:hover {
  color: white;
}

.header-menu .social-list {
  margin: 0 auto;
  margin-top: 40px;
  justify-content: center;
}

.header-menu .social-list-wrap {
  width: 100%;
}

.header-menu .call-us__title {
  font-size: 16px;
}

.header-menu .call-us__link {
  font-size: 18px;
}

.header-menu-list {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.header-menu-list-wrap {
