  flex-grow: 1;
}

.icon-load {
  display: flex;
  flex-direction: column;
  width: 90px;
  height: 70px;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-top: -45px;
  margin-left: -35px;
  display: none;
}

.icon-load__line {
  margin-top: 25px;
  width: 100%;
  height: 2px;
  background-color: #7A71FF;
  position: relative;
}

.icon-load__line-progress {
  position: absolute;
  width: 10%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #09C806;
  transition: all 1s;
}

.icon-load__line-progress.active {
  width: 100%;
}

.loaded .icon-load {
  display: block;
}

.no-scroll {
  overflow: hidden;
}

.show-tab {
  display: none;
}

