.btn {
  display: inline-block;
  width: 100%;
  max-width: 220px;
  font-weight: bold;
  line-height: normal;
  font-size: 14px;
  text-align: center;
  color: #1E1E1E;
  padding: 15px 25px;
  border: 2px solid #0FCB0B;
  background-color: #fff;
  border-radius: 120px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  transition: 0.3s ease-out;
}

.btn span {
  display: inline-block;
  transition: 0.3s ease-out;
  opacity: 1;
  position: relative;
  cursor: pointer;
}

.btn::before {
  content: attr(data-link);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 150%);
  font-weight: bold;
  line-height: normal;
  font-size: 14px;
  text-align: center;
  white-space: nowrap;
  transition: 0.3s ease-out;
  opacity: 0;
}

.btn:hover span {
  transform: translateY(-200%);
  opacity: 0;
}

.btn:hover::before {
  transform: translate(-50%, -50%);
  opacity: 1;
}

.btn-fill {
  background: #776AFF;
  border: 2px solid #776AFF;
  color: #fff;
  width: 100%;
  max-width: 260px;
}

.btn-green {
  background: #0FCB0B;
  border: 2px solid #0FCB0B;
}

.btn-disabled {
  background: #E5E5E5;
  color: #1E1E1E;
  border: 2px solid #E5E5E5;
}

.btn-disabled:hover {
  cursor: not-allowed;
}

.btn-disabled:hover span {
  transform: none;
  opacity: 1;
  cursor: not-allowed;
}

.btn-disabled:hover::before {
  display: none;
}

