.datepicker {
  width: 320px;
  border: 1px solid #DADADA;
  box-shadow: 0px 14px 40px rgba(0, 0, 0, 0.038);
  border-radius: 10px;
}

.datepicker.-center-center- {
  top: 100px;
  left: 0;
}

.datepicker--day-name {
  line-height: 19px;
  font-size: 14px;
  color: #4a4a4a;
  font-family: "Open Sans", "Roboto", sans-serif;
}

.datepicker--day-name.-weekend- {
  color: #C5C5C5;
}

.datepicker--cell {
  height: 42px;
  font-weight: bold;
  line-height: 21px;
  font-size: 18px;
  border-bottom: 0.5px solid #E5ECED;
  border-radius: 0;
  color: #4A4A4A;
  font-family: "Gilroy", sans-serif;
}

.datepicker--cell-day.-other-month-:empty {
  border-bottom: 0.5px solid #E5ECED;
}

.datepicker--cell.-current- {
  color: #000;
}

.datepicker--cell.-disabled- {
  color: #C5C5C5;
}

.datepicker--cell.-disabled-.-focus- {
  background: none;
  color: #C5C5C5;
}

.datepicker--cell.-weekend- {
  color: #C5C5C5;
}

.datepicker--cell.-weekend-.-focus- {
  background: none;
  color: #C5C5C5;
}

.datepicker--cell.-selected- {
  background: transparent;
  position: relative;
  border-bottom: 0.5px solid #E5ECED;
}

.datepicker--cell.-selected-:before {
  content: '';
  position: absolute;
  z-index: -1;
  left: 10%;
  top: 10%;
  width: 80%;
  height: 80%;
  background: #0FCB0B;
  border-radius: 15px;
}

.datepicker--cell.-selected-.-focus- {
  background: transparent;
}

.datepicker--content {
  padding: 14px 0;
}

.datepicker--time {
  padding: 22px 25px;
  border-top: none;
}

.datepicker--pointer {
  display: none;
}

.datepicker--time:hover .datepicker--time-sliders:before {
  opacity: 0;
  z-index: -1;
}

.datepicker--time-sliders {
  position: relative;
}

.datepicker--time-sliders:before {
  content: 'Укажите время';
  position: absolute;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 5;
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #5B5B5B;
}

.datepicker--time,
.datepicker--time-current-hours,
.datepicker--time-current-minutes {
  flex-direction: row-reverse;
  justify-content: space-between;
  font-size: 48px;
  color: #5B5B5B;
  font-family: "Gilroy", sans-serif;
}

.datepicker--time-current {
  display: flex;
  justify-content: flex-end;
}

.datepicker-box {
  z-index: 5;
  position: absolute;
}

