@media screen and (max-width: 1023px) {
  .catalog-slider__info {
    opacity: 1;
  }
  .form-filter {
    width: 100%;
    display: flex;
    margin: 0 -20px;
    flex: 1 1 auto;
    padding-right: 0;
  }
  .form-filter .col {
    width: 50%;
    padding: 0 20px;
  }
  .form-filter__title {
    padding-top: 0;
  }
  .box-constructor__wrap {
    flex-wrap: wrap;
  }
  .constructor {
    width: 100%;
    margin-top: 35px;
  }
}

@media screen and (max-width: 767px) {
  .constructor-slider .slick-arrow.slick-prev {
    left: 10px;
  }
  .constructor-slider .slick-arrow.slick-next {
    right: 10px;
  }
  .catalog-slider .slick-arrow.slick-prev {
    left: -42px;
  }
  .catalog-slider .slick-arrow.slick-next {
    right: -42px;
  }
  .constructor__wrap {
    width: 100%;
    min-width: 620px;
  }
  .constructor-slider {
    user-select: auto;
    touch-action: auto;
  }
  .breadcrumbs-list-wrap {
    padding: 70px 0 0;
  }
  .box-constructor__title {
    font-size: 25px;
    text-align: center;
  }
}

@media screen and (max-width: 500px) {
  .reload {
    margin-bottom: 15px;
  }
  .box-constructor__top {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .form-filter {
    flex-wrap: wrap;
    margin: 0;
  }
  .form-filter .col {
    width: 100%;
    padding: 0;
  }
  .form-filter .col:nth-child(2) {
    text-align: center;
  }
  .form-filter .col:nth-child(2) .form-filter__checkbox {
    text-align: left;
  }
  .form-filter__title {
    padding-top: 20px;
  }
  .box-constructor__title {
    font-size: 22px;
  }
}

