  position: relative;
}

.eye-list__link i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.eye-list__link:hover {
  color: #1E1E1E;
}

.constructor-slider .slick-dots li:nth-child(9) {
  position: absolute;
  left: calc(100% + 5px);
  top: 0;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 5px;
  speak: none;
  font-style: normal;
  font-weight: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  font-family: icomoon !important;
  font-variant: normal;
}

.constructor-slider .slick-dots li:nth-child(9):before {
  content: '\e921';
  position: absolute;
  top: 6px;
  left: 100%;
  color: #FFFFFF;
  opacity: 0.6;
  font-size: 7px;
  transform: rotate(-90deg);
}

.constructor-slider .slick-dots li:nth-child(10) {
  position: absolute;
  left: calc(100% + 39px);
  top: 0;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 5px;
