}

.measurements-list__input-wrap {
  position: relative;
}

.measurements-list__input-wrap span {
  position: absolute;
  font-weight: bold;
  font-size: 16px;
  line-height: normal;
  color: #1E1E1E;
  right: 10px;
  top: 5px;
}

.measurements-list__input-wrap input {
  padding-right: 20px;
}

.btn-fill.disabled {
  border-color: #ccc;
  background: #ccc;
  cursor: default;
}

.btn-fill.disabled input {
  cursor: default;
}

@media screen and (max-width: 1023px) {
  .big-layout__img {
    width: 60%;
  }
  .eye-list__link {
    width: 25px;
    height: 25px;
    font-size: 9px;
  }
  .box-constructor__top_right-form {
    padding-right: 0;
  }
  .box-constructor__top_right-form + .box-constructor__wrap {
    flex-direction: column-reverse;
  }
  .form-filter_right {
    padding-left: 0;
  }
  .table-list .col {
    font-size: 13px;
  }
  .apartment-layouts__tooltipe {
    left: 10px;
    max-width: calc(100% - 20px);
    white-space: normal;
  }
}

@media screen and (max-width: 767px) {
  .big-layout {
    display: block;
    margin-top: 14px;
    margin-bottom: 15px;
  }
  .big-layout__row {
    display: block;
  }
  .big-layout__cont {
    padding: 0;
    text-align: center;
  }
  .big-layout__cont .choose {
    justify-content: center;
  }
  .big-layout__cont .reload {
    justify-content: center;
  }
  .big-layout__img {
    width: 100%;
    margin-bottom: 20px;
  }
  .big-layout h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .table-list {
    min-width: 600px;
  }
  .box-constructor__top_right-form h2 {
    font-size: 20px;
  }
  .box-constructor-table h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .table-list .col {
    height: 45px;
  }
  .choose-apartment__row {
    display: block;
    margin-bottom: 20px;
  }
  .choose-apartment__label {
    width: 100%;
    padding: 0;
    margin-bottom: 8px;
    display: block;
  }
  .apartment-layouts__item {
    width: 50%;
  }
  .apartment-check {
    margin: 0 -3px;
  }
  .apartment-check__item {
    padding: 0 3px;
  }
  .apartment-check__link {
    font-size: 12px;
  }
  .choose-apartment__title {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .choose-design__title {
    font-size: 20px;
  }
  .choose-design-slider {
    padding: 0 50px;
  }
}

@media screen and (max-width: 480px) {
  .eye-list__link {
    width: 20px;
    height: 20px;
    font-size: 7px;
  }
  .choose-design__title {
    font-size: 18px;
    line-height: 24px;
  }
  .apartment-layouts__item {
    width: 100%;
    margin-bottom: 15px;
  }
  .apartment-layouts__item:last-child {
    margin-bottom: 0;
  }
  .choose-apartment__row {
    margin-bottom: 12px;
  }
  .apartment-check {
    flex-wrap: wrap;
  }
  .apartment-check__item {
    width: 33.3%;
    margin-bottom: 6px;
  }
  .choose-design__btn {
    padding-left: 0;
    text-align: center;
  }
  .apartment-layouts__img {
    width: 100px;
  }
  .apartment-layouts__img img {
    max-width: 100%;
  }
}
